.Dairy {
    display: flex;
    font-family: Verdana, sans-serif;
    flex-flow: row nowrap;
    min-height: 100vh;
    min-width: 500px;
}

@media (max-width: 500px) {
    .Dairy {
        flex-wrap: wrap;
        height: auto;
    }
}
.NotesAddForm {
    flex-flow: row nowrap;
    display: flex;
    margin-top: 20px;
}

.NotesAddForm > .avatar {
    background-color: #e6e6e6;
    border-radius: 5px;
    margin: 10px 10px 10px 0;
    width: 50px;
    height: 50px;
}

.NotesAddForm > .message {
    margin: 10px 0 10px 10px;
    flex: 1 1;
    height: 50px;
}

.NotesList {
    overflow-y: auto;
    flex: 1 1;
}

.NotesListItem {
    border-bottom: 1px solid #f2f2f2;
    flex-flow: row nowrap;
    display: flex;
}

.NotesListItem:last-child {
    border-bottom: none;
}

.NotesListItem > .avatar {
    background-color: #ff2f5a;
    border-radius: 5px;
    margin: 10px 10px 10px 0;
    width: 50px;
    height: 50px;
}

.NotesListItem > .message {
    margin: 10px 0 10px 10px;
    flex: 1 1;
    word-break: break-all;
}

.Panel {
    background-color: #ffffff;
    box-shadow: 0 0 5px #727376;
    display: flex;
    flex-flow: column nowrap;
    flex: 1 1 40%;
    padding: 20px;
    margin: 10px;
}

.Panel > .title {
    border-bottom: 1px solid #f2f2f2;
    font-size: 25px;
    color: #616573;
    margin-bottom: 10px;
}

.Panel > .body {
    display: flex;
    flex-flow: column nowrap;
    flex: 1 1;
    height: 0;
}

@media (max-width: 1000px) {
    .Panel {
        flex: 1 1 100%;
    }

    .Panel > .body {
        max-height: 50vh;
    }
}

@media (max-width: 500px) {
    .Panel > .body {
        max-height: 100vh;
        height: auto;
    }
}

.PanelGroup {
    background-color: #f9f9f9;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    flex: 1 1;
    overflow-y: auto;
    padding: 10px;
}

@media (max-width: 500px) {
    .PanelGroup {
        height: auto;
        width: 100%;
    }
}

.PostsAddForm {
    margin-bottom: 10px;
    display: flex;
}

.PostsAddForm > * {
    margin-right: 10px;
}

.PostsAddForm > *:last-child {
    margin-right: 0;
}

.PostsList {
    flex-flow: column;
    overflow-y: auto;
    flex: 1 1;
}

.PostsListItem {
    align-content: center;
    border-bottom: 1px solid #f2f2f2;
    flex-flow: row nowrap;
    display: flex;
    position: relative;
    padding: 5px 0;
}

.PostsListItem:last-child {
    border-bottom: none;
}

.PostsListItem > .active {
    background-color: #ff2f5a;
    height: 100%;
    width: 5px;
    position: absolute;
    left: 0;
    top: 0;
}

.PostsListItem > .title {
    cursor: pointer;
    margin: auto auto auto 20px;
    flex: 1 1;
    width: 0;
}

.PostsListItem > .title > .title-wrap {
    display: inline-block;
    max-width: 85%;
    text-overflow: ellipsis;
    vertical-align: middle;
    overflow: hidden;
    white-space: nowrap;
}

.Sidebar {
    background-color: #2b2f3e;
    color: #ffffff;
    padding: 20px;
    width: 200px;
}

.Sidebar > .title {
    text-transform: uppercase;
    font-size: 25px;
    margin-bottom: 10px;
}

.Sidebar > .description {
    color: #727376;
}

@media (max-width: 500px) {
    .Sidebar {
        height: auto;
        padding: 15px;
        width: 100%;
    }

    .Sidebar > .title {
        margin-bottom: 5px;
    }
}

html, body {
    padding: 0;
    font-size: 13px;
    margin: 0;
}

